body {
   position: relative;
   min-width: 320px;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   @include f("gp400", 18, 28);
   color: $color1;
   @include xs {
      font-size: 16px;
      line-height: 26px; }
   &.is-locked {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      height: 0;
      overflow: hidden; } }
.container {
   max-width: 1170px;
   margin: 0 auto;
   padding: 0 15px; }
.row {
   display: flex;
   flex-wrap: wrap;
   margin: 0 -15px; }
.main {
   overflow: hidden; }
.section-title {
   $parent: &;
   position: relative;
   padding-bottom: 20px;
   @include xs {
      padding-bottom: 15px; }
   &_left {
      text-align: left;
      &::after {
         left: 0; } }
   &_center {
      text-align: center;
      &::after {
         left: 50%;
         margin-left: -25px; } }
   &::after {
      position: absolute;
      bottom: 0;
      width: 50px;
      height: 2px;
      background-color: $color5;
      content: ""; } }

